import { Component, OnInit } from '@angular/core';
import { TipoSolicitudService } from 'src/app/commons/services/aerc-services/tipo-solicitud.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { TipoSolicitudModel } from '../../../../commons/models/aerc-models/tipo-solicitud-model';
import { ToastrService, ToastrModule } from 'ngx-toastr';

@Component({
  selector: 'app-admin-solicitudes',
  templateUrl: './admin-solicitudes.component.html',
  styles: [
  ]
})
export class AdminSolicitudesComponent implements OnInit {

  public listadoTipoSolicitud: TipoSolicitudModel[] = [];

  constructor(
    private tipoSolicitudService: TipoSolicitudService,
    private alert: UtilAlert,
    private toastr: ToastrModule,

  ) { }

  //* INICIACION DE LA VENTANA
  //* ----------------------------------------------------------------------------------

  ngOnInit(): void {
    this._listarTipos();
  }

  ngOnDestroy(): void {
  }

  private _listarTipos() {
    this.tipoSolicitudService.getAllTipoSolicitud().subscribe((result) => {
      this.listadoTipoSolicitud = result;

      // console.log(this.listadoTipoSolicitud);
    })
  }

  public activar(tipoSolicitud: TipoSolicitudModel, estado: 'ESCUELA' | 'VRA') {
    let tipoSolicitudTemp = { ...tipoSolicitud };
    // console.log(tipoSolicitud);
    if (estado == 'ESCUELA') {
      // console.log("ENTRO")
      tipoSolicitudTemp.activoEscuela = true;
    }
    if (estado == 'VRA') {
      tipoSolicitudTemp.activoVra = true;
    }
    // console.log(tipoSolicitudTemp)
    this._saveTipoSolicitud(tipoSolicitudTemp, 'ACTIVAR');
  }

  public desactivar(tipoSolicitud: TipoSolicitudModel, estado: 'ESCUELA' | 'VRA') {
    let tipoSolicitudTemp = { ...tipoSolicitud };
    if (estado == 'ESCUELA') {
      tipoSolicitudTemp.activoEscuela = false;
    }
    if (estado == 'VRA') {
      tipoSolicitudTemp.activoVra = false;
    }
    this._saveTipoSolicitud(tipoSolicitudTemp, 'DESACTIVAR');
  }

  private _saveTipoSolicitud(tipoSolicitud: TipoSolicitudModel, accion: 'ACTIVAR' | 'DESACTIVAR') {
    this.tipoSolicitudService.saveTipoSolicitud(tipoSolicitud).subscribe(result => {
      if (result) {
        if (accion == 'ACTIVAR') {
          this.alert.toastInfo("Se ha activado el tipo de solicitud: " + tipoSolicitud.descripcion);
        }
        if (accion == 'DESACTIVAR') {
          this.alert.toastInfo("Se ha desactivado el tipo de solicitud: " + tipoSolicitud.descripcion);
        }
        this._listarTipos();
      } else {
        this.alert.toastError("Ha ocurrido un error al momento de actualizar el tipo de solicitud.");
      }
    })
  }

}





